/* user.css */
.dashboard-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 900px;
  padding: 10px;
  background-color: #f8f9fa;
  border-radius: 10px;
  position: relative;
}

.dashboard-logo {
  height: 40px;
  cursor: pointer;
}

.profile-circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #007bff;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  cursor: pointer;
}

.profile-menu {
  position: absolute;
  right: 10px;
  top: 50px;
  background: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  padding: 10px;
  text-align: center;
}

.profile-menu .close-menu {
  border: none;
  background: transparent;
  font-size: 18px;
  cursor: pointer;
}

.user-name, .user-email {
  margin: 5px 0;
}

.logout-btn {
  background: #dc3545;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 5px;
  cursor: pointer;
}

.greeting {
  margin: 20px 0;
}

.courses-container {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.course-card {
  width: 280px;
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.course-card img {
  width: 100%;
  height: 160px;
  object-fit: cover;
}

.course-card h3 {
  margin: 10px 0;
}

.course-card p {
  padding: 0 10px 10px;
  font-size: 14px;
}


  /* Responsive Adjustments */
  @media (max-width: 768px) {
    .dashboard-header {
      flex-direction: row;
      justify-content: space-between;
    }
  
    .greeting {
      font-size: 20px;
    }
  }
  