/* Chat Container */
.chat-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-color: #f0f0f0;
    padding: 10px;
  }
  
  /* Chat Header */
  .chat-header {
    background-color: #007aff;
    color: white;
    padding: 15px;
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    border-radius: 8px;
  }
  
  /* Chat Messages */
  .chat-messages {
    flex: 1;
    overflow-y: auto;
    padding: 10px;
    display: flex;
    flex-direction: column;
  }
  
  .message {
    max-width: 60%;
    padding: 10px;
    border-radius: 10px;
    margin: 5px;
    word-wrap: break-word;
  }
  
  .sent {
    align-self: flex-end;
    background-color: #007aff;
    color: white;
  }
  
  .received {
    align-self: flex-start;
    background-color: #e0e0e0;
    color: black;
  }
  
  /* Chat Input */
  .chat-input {
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: white;
    border-top: 1px solid #ddd;
  }
  
  .chat-input input {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 20px;
    outline: none;
  }
  
  button {
    margin-left: 10px;
    padding: 10px 15px;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    font-size: 14px;
  }
  
  .record {
    background-color: #28a745;
    color: white;
  }
  
  .recording {
    background-color: red;
    color: white;
  }
  
  .send {
    background-color: #007aff;
    color: white;
  }
  